











































































































import shoppublic from '@/common/shoppublic'; //服务器地址
export default {
	data() {
		return {
			// 测试数据
			tabsState:'',//选项卡状态
			tabsMaskState:'',//选项卡遮罩显示隐藏
			swiperIndex:'',
			blockJobWanted:true,//求职状态
			blockRecruit:false,//招聘状态
			BlockPositionTop:false,//固定顶部的遮罩显示
			recruitList:[],
			jobWantedList:[],
			top_scrollTop:'',
			blockInputNav:false,
			pagesize: 0,
			index:7,
			status:1
		};
	},
	onPageScroll(e){
		// console.log(e.scrollTop);
		this.top_scrollTop = e.scrollTop;
		if(e.scrollTop >= 100){
			this.BlockPositionTop = true;
			this.blockInputNav = true;
		}else{
			this.BlockPositionTop = false;
			this.blockInputNav = false;
		}
	},
	onLoad() {
		this.getPageInfo();
	},
	onReachBottom() {
		this.getPageInfo('onReach');
	},
	methods:{
		// 获取招聘页面信息
		getPageInfo(page = '0'){
			var _this = this;
			var oldPartnerList = _this.recruitList;
			// *************当index等于28时会报错**************
			if(page === 'onReach'){
				uni.request({
					url:shoppublic.getUrl() + 'storeRecruitment/findStoreRecruitmentList',
					data:{
						index:_this.index,
						county:'',
						city:610100,
						salarymin:'',
						salarymax:'',
						type:'',
						typevalue:'',
						status:_this.status
					},
					success: (res) => {
						if(_this.status === 1){
							console.log(res.data.responseBody);
							if(page === 'onReach' && res.data.responseBody){
								oldPartnerList = oldPartnerList.concat(res.data.responseBody);
								_this.index += 7;
							}else{
								return false;
							}
							_this.recruitList = oldPartnerList;
						}else{
							console.log(res.data.responseBody);
							if(page === 'onReach' && res.data.responseBody){
								oldPartnerList = oldPartnerList.concat(res.data.responseBody);
								_this.index += 7;
							}else{
								return false;
							}
							_this.jobWantedList = oldPartnerList;
						}
						
					}
				})
			}else{
				uni.request({
					url:shoppublic.getUrl() + 'storeRecruitment/findStoreRecruitmentList',
					data:{
						index:_this.pagesize,
						county:'',
						city:610100,
						salarymin:'',
						salarymax:'',
						type:'',
						typevalue:'',
						status:_this.status
					},
					success: (res) => {
						if(_this.status === 1){
							console.log(res.data.responseBody);
							_this.recruitList = res.data.responseBody;
						}else{
							console.log(res.data.responseBody);
							_this.jobWantedList = res.data.responseBody;
						}
						
					}
				})
			}
		},
		// 金钱换算函数
		moneyConversion(money) {
			// console.log(money);
			var moneyP = 0;
			var moneyT = null;
			var moneyF = null;
			money <= 100 ? moneyP = money * 0.001 : moneyP = money;
			money <= 1000 ? moneyP = money * 0.001 : moneyP = money;
			money <= 10000 ? moneyP = money * 0.001 : moneyP = money;
			money <= 100000 ? moneyP = money * 0.001 : moneyP = money;
			money <= 1000000 ? moneyP = money * 0.001 : moneyP = money;
			// moneyF = moneyP.toString();
			// moneyT = moneyM.split('.');
			moneyT = Math.round(moneyP);
			return moneyT + 'K';
		},
		// 返回上一页
		backPreviousPage(){
			// console.log(getCurrentPages());
			uni.switchTab({
				url: '/pages/index/index'
			});
		},
		// 区域/费用选择器按钮点击事件
		clickScrollCell(e){
			this.closeTabsMask();
		},
		// 顶部选项卡点击事件
		tabsChanges(state){
			this.tabsState = state;
			this.swiperIndex = state;
			this.tabsMaskState = 'true';
		},
		// 选项卡滑块切换
		swiperChangs(e){
			this.tabsState = e.detail.current;
			this.swiperIndex = e.detail.current;
		},
		// 关闭遮罩
		closeTabsMask(){
			this.tabsState = '';
			this.tabsMaskState = '';
			this.swiperIndex = '';
		},
		// 跳转发布详情
		toLocationRelease(){
			uni.navigateTo({
				url:"./recruitAndSeekRelease/recruit/recruit"
			})
		},
		//跳转搜索:0是主页跳转
		toSearch(e) {
			uni.navigateTo({
				url: '../../searchPage/searchPage?search_state=' + '0'
			});
		},
		// 切换样式
		switchStyle(index){
			// console.log(index);
			if(index === '1'){
				this.blockJobWanted = false;
				this.blockRecruit = true;
				this.status = 2;
				this.getPageInfo();
			}else if(index === '2'){
				this.blockJobWanted = true;
				this.blockRecruit = false;
				this.status = 1;
				this.getPageInfo();
			}
		},
		toRecruitAndSeekDateils(e,id){
			if(e === '1'){
				console.log(1);
				uni.navigateTo({
					url:'./recruitAndSeekDateils/recruitAndSeekDateils?id=' + id
				})
			}else if(e === '2'){
				console.log(2);
				uni.navigateTo({
					url:'./recruitAndSeekDateils/recruitAndSeekDateils?id=' + id
				})
			}
		}
	}
	
}
